import { Component, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router, RouterModule } from '@angular/router';
import {
  AppNameService, DomainListBase, EventbusService,
  MainBarTitleService, TooltipComponent, WindowService
} from '@front-shared-lib';
import { AdminModalContainerComponent } from '../../../misc/admin-modal-container/admin-modal-container.component';
import { AdminPageContentComponent } from '../../../misc/admin-page-content/admin-page-content.component';
import { AdminTableComponent } from '../../../misc/admin-table/admin-table.component';
import { AdminTopBarComponent } from '../../../misc/admin-top-bar/admin-top-bar.component';
import { AdminSetUpDnsComponent } from '../admin-set-up-dns/admin-set-up-dns.component';
import { AutoDnsDomain } from '@main-shared-lib';


@Component({
  selector: 'app-admin-domain-list',
  standalone: true,
  imports: [
    RouterModule,
    TooltipComponent,
    AdminTableComponent,
    AdminPageContentComponent,
    AdminTopBarComponent,
    AdminSetUpDnsComponent,
    AdminModalContainerComponent,
    AdminSetUpDnsComponent
  ],
  templateUrl: './admin-domain-list.component.html',
  styleUrl: './admin-domain-list.component.scss'
})
export class AdminDomainListComponent
  extends DomainListBase
  implements OnInit, OnDestroy {
  showSetupDnsModal = false;
  domain?: AutoDnsDomain;

  constructor(
    protected override windowService: WindowService,
    protected override router: Router,
    protected override eventbus: EventbusService,
    protected override apNme: AppNameService,
    public override mainBarTitle: MainBarTitleService,
    protected override metaTagService: Meta,
    protected override titleService: Title
  ) {
    super(windowService, router, eventbus, apNme, mainBarTitle, metaTagService, titleService);
  }

  override setupDns() {
    this.showSetupDnsModal = true;
  }

  closeSetupDnsModal() {
    this.showSetupDnsModal = false;
  }


  override ngOnDestroy() {
    super.ngOnDestroy();
  }
}
