import { Directive, HostListener, Input, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { Company } from '@biz-nest/commerce-client';
import { FrontendLogger, IfilterProps } from '@biz-nest/core-client';
import {
  AppNameService, EventbusService, MainBarTitleService,
  scrollToTop, WindowService
} from '@front-shared-lib';
import { AutoDnsDomain, navByBus, runSubscriptionsForSharedUse } from '@main-shared-lib';


@Directive()
export class DomainListBase
implements OnInit, OnDestroy {
  @Input() company!: Company;
  loading = false;
  domains: AutoDnsDomain[] = [];
  origDomains: AutoDnsDomain[] = [];
  posting = false;
  selections: string[] = [];
  isDeletingMany = false;
  cleanup?: () => void;
  getErr?: string;
  showModal = false;;
  filterProps: IfilterProps = {};
  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;

  currentActivePopupId = '';

  actions = [
    {
      label: 'delete',
      icon: 'delete',
      event: (item) => this.deleteDomainRel(item.id)
    }
  ];

  constructor(
    protected windowService: WindowService,
    protected router: Router,
    protected eventbus: EventbusService,
    protected apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    protected metaTagService: Meta,
    protected titleService: Title
  ) { }

  @HostListener('document:click', ['$event'])
  onDocumentClick() {
    this.currentActivePopupId = '';
  }

  ngOnInit(): void {
    this.getDomains();
    this.mainBarTitle.title = 'All Domains - Manage Domains';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: 'Easily manage and view all company domains for effective communication tracking.'
    });

    const handlers = {
    };

    const { cleanup } = runSubscriptionsForSharedUse(this.eventbus, handlers);

    this.cleanup = cleanup;
    scrollToTop(this.windowService);
  }

  openExtras(id: string) {
    if (this.currentActivePopupId === id) {
      this.currentActivePopupId = '';

      return;
    }
    this.currentActivePopupId = id;
  }

  async getDomains() {
    try {
      this.loading = true;
      this.getErr = '';
      const domains = await AutoDnsDomain.getAll();

      this.domains = domains;
      this.origDomains = domains;
    } catch (error) {
      FrontendLogger.error(error);
    } finally {
      this.loading = false;
    }
  }

  deleteDomainRel(domain: AutoDnsDomain) {
    if (!domain) {
      FrontendLogger.error('DomainListComponent:deleteDomainRel:: - domain is undefined');

      return;
    }
    this.deleteDomain(domain);
  }


  async deleteDomain(domain: AutoDnsDomain) {
    try {
      await domain.remove();
      this.domains.length = 0;
      await this.getDomains();
    } catch (error) {
      FrontendLogger.error('DomainListComponent:getDomains:: - err ', error);
    }
    this.showModal = false;
  }

  setupDns() {}

  sortBy(val: string, direction: 'asc' | 'desc') {
    this.domains = this.origDomains.sort((a, b) => {
      if (direction === 'asc') {
        if (a[val] > b[val]) {
          return -1;
        } else if (a[val] < b[val]) {
          return 1;
        } else {
          return 0;
        }
      } else if (a[val] > b[val]) {
        return 1;
      } else if (a[val] < b[val]) {
        return -1;
      } else {
        return 0;
      }
    });
  }

  ngOnDestroy() {
    this.cleanup?.();
  }
}
